import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import * as Yup from 'yup';
import { Formik } from 'formik';

import Model from '../../../components/UI/Model/Model';

import { COMMISSION_PERCENTAGE_REQUIRED } from '../../../constants/errorConstants';
import { editPromoterCommissionSaga } from '../../../store/actions';

const PromoterCommissionModel = props => {
  const dispatch = useDispatch();
  const { promoterId } = props;
  const [isSubmitted, setIsSubmitted] = useState(false);
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const validationSchema = Yup.object({
    commission: Yup.number()
      .typeError('Commission must be a number')
      .label('Commission')
      .min(1)
      .max(100)
      .required(COMMISSION_PERCENTAGE_REQUIRED),
  });

  const submitBtnHandler = values => {
    dispatch(
      editPromoterCommissionSaga({
        id: promoterId,
        isUpdated: props.isUpdated === true,
        ...values,
        tableFilter: props.tableFilter,
        closeModel,
        setIsSubmitted,
      }),
    );
    setIsSubmitted(true);
  };

  return (
    <Formik
      initialValues={{
        commission: props.commissionPercentage
          ? props.commissionPercentage
          : '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        // isSubmitting,
      }) => (
        <Model
          closeModel={closeModel}
          modalId="modal-delete"
          headerTitle={
            props.isUpdated === true
              ? 'Are you sure you want to update commission?'
              : 'Are you sure you want to accept?'
          }
        >
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <h4 className="required-field">
                Please enter the mutually agreed commission rate(%):
              </h4>
              <input
                type="text"
                className="form-control"
                name="commission"
                value={values.commission}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.commission && touched.commission && errors.commission}
              </div>
            </div>
            <div className="clearfix m-t-md">
              <button
                type="submit"
                className="btn btn-primary"
                data-toggle="modal"
                data-dismiss="modal"
                data-target="#modal-successb"
                disabled={isSubmitted}
              >
                Submit
              </button>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default PromoterCommissionModel;
