import React, { useState } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import Model from '../../../components/UI/Model/Model';
import { addSubadminSaga } from '../../../store/actions';
import {
  FIRSTNAME_REQUIRED,
  LASTNAME_REQUIRED,
  EMAIL_REQUIRED,
  EMAIL_VALID,
  PASSWORD_VALIDATION,
  PASSWORD_REQUIRED,
  PHONE_REQUIRED,
} from '../../../constants/errorConstants';

function AddPromoterModel(props) {
  // eslint-disable-next-line no-unused-vars
  const { tableFilter } = props;
  const [isDisable, setIsDisable] = useState(false);

  const dispatch = useDispatch();

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const validationSchema = Yup.object({
    firstName: Yup.string().trim().required(FIRSTNAME_REQUIRED),
    lastName: Yup.string().trim().required(LASTNAME_REQUIRED),
    phone: Yup.string()
      .label('Phone')
      .trim()
      .min(10)
      .max(10)
      .required(PHONE_REQUIRED),
    email: Yup.string().email().typeError(EMAIL_VALID).required(EMAIL_REQUIRED),
    password: Yup.string()
      .matches(
        /^(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
        PASSWORD_VALIDATION,
      )
      .required(PASSWORD_REQUIRED),
  });

  const { errorMsg } = useSelector(state => state.subadmin);

  const submitBtnHandler = values => {
    console.log(values);
    setIsDisable(true);
    dispatch(
      addSubadminSaga({
        // firstName,
        // lastName,
        // email,
        // phone,
        isDeleted: true,
        // password,
        tableFilter,
        closeModel,
        setIsDisable,
        ...values,
      }),
    );
  };

  return (
    <Formik
      initialValues={{
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        password: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
      }) => (
        <Model
          closeModel={closeModel}
          headerTitle={props.isEdit ? 'Edit Promoter' : 'Add Promoter'}
          submitBtnText={props.isEdit ? 'Edit' : 'Add'}
          submitBtnHandler={handleSubmit}
          isDisable={isDisable}
          errorMsg={errorMsg}
        >
          <form>
            <div className="from-group text-left">
              <h4 className="required-field">First Name:</h4>
              <input
                type="text"
                name="firstName"
                placeholder="Enter First Name"
                className="form-control"
                value={values.firstName}
                // onChange={e => setFirstName(e.target.value.trim())}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.firstName && touched.firstName && errors.firstName}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Last Name:</h4>
              <input
                type="text"
                name="lastName"
                placeholder="Enter Last Name"
                className="form-control"
                value={values.lastName}
                // onChange={e => setLastName(e.target.value.trim())}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.lastName && touched.lastName && errors.lastName}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Phone Number:</h4>
              <input
                type="number"
                name="phone"
                placeholder="Enter Phone Number"
                className="form-control"
                value={values.phone}
                onChange={handleChange}
                onBlur={handleBlur}
                // onChange={e => setPhone(e.target.value.trim())}
              />
              <div className="error-message">
                {errors.phone && touched.phone && errors.phone}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Email:</h4>
              <input
                type="text"
                placeholder="Enter Email"
                name="email"
                className="form-control"
                value={values.email}
                // onChange={e => setEmail(e.target.value.trim())}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.email && touched.email && errors.email}
              </div>
            </div>
            <div className="from-group text-left">
              <h4 className="required-field">Password:</h4>
              <input
                type="password"
                name="password"
                placeholder="Enter Password"
                className="form-control"
                value={values.password}
                // onChange={e => setPassword(e.target.value.trim())}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.password && touched.password && errors.password}
              </div>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
}

export default AddPromoterModel;
